// Requests plugin.
import { RESPONSE } from './interface/actionTypes'
import * as actions from './interface/actions'

// Libraries.
import { mergeValues } from '../common/utils'
import { get } from 'lodash/fp'
import { put, take, call, select } from 'redux-saga/effects'
import { injectAgentVersionHeader } from './interface/selectors'
import { getPlatform } from '../auth/interface/selectors'
import { getVersion } from '../common/version'

// Constants
import { platforms } from '../constants'

/**
 * Creates an effect description that instructs the middleware to perform a request with the provided options.
 * This effect is blocking and will yield a RESPONSE action on completion.
 *
 * @param {Object} options See https://developer.mozilla.org/en-US/docs/Web/API/Request/Request
 * @param {string} options.url The url to perform the request on.
 * @param {string} options.method The HTTP method to use for the request.
 * @param {Object} options.headers Object literal of headers you want to add to the request.
 * @param {Object} [options.queryParams] The parameters to be added to the query string
 * @param {string} [options.responseType] The data type assumed to be received in the response body
 * @param {Blob|BufferSource|FormData|UrlSearchParams|string} [options.body] Any body that you want to add to your request.
 * @return A blocking redux-saga effect that will instruct the middleware to wait for the request to be fullfilled or until it fails.
 */
export default function request (options, commonOptions) {
  return call(requestSaga, options, commonOptions)
}

/*
 * The saga backing the request effect.
 */
export function * requestSaga (options, commonOptions) {
  // Merge any extra request options into the provided options
  //      for this request. Priority is for the passed-in options.
  options = mergeValues(commonOptions, options)

  const useCustomHeader = yield select(injectAgentVersionHeader)
  if (useCustomHeader) {
    let platform = yield select(getPlatform)

    // Assume request is for CPaaS platform, by default.
    let headerValue = `cpaas-js-sdk/${getVersion()}`

    // Check if request is for callMe service, otherwise determine the apropriate platform.
    // (callMe service uses Link platform for call requests)
    if (options.url && options.url.includes('/anonymous/')) {
      headerValue = `callme-js-sdk/${getVersion()}`
    } else {
      // Check the actual platform used
      if (platform === platforms.UC) {
        headerValue = `uc-js-sdk/${getVersion()}`
      } else if (platform === platforms.LINK) {
        headerValue = `link-js-sdk/${getVersion()}`
      }
    }

    // Note that the same headerName is used for all platforms & services.
    options = mergeValues(options, {
      headers: {
        'X-Cpaas-Agent': headerValue
      }
    })
  }

  const requestAction = yield put(actions.request(options))
  const responseAction = yield take(
    action => action.type === RESPONSE && get('meta.requestId', action) === requestAction.meta.requestId
  )

  return responseAction
}
